<script>
import { onMounted } from "vue"
import login from '@/mixins/auth/login';
export default {
    name: "Map-Box-Svg",
    setup(props, context) {

        onMounted(() => {
            $(document).ready(function () {
                $('[data-toggle="popover"]').popover({
                    placement: 'top',
                    html: true
                });

                let paths = document.querySelectorAll(".map--view path");
                const pathArray = Array.from(paths);
                /*path color*/
                context.emit("getRegions", pathArray);
            });
        })
        return {}
    },
}
</script>


<style>
.map--view path {
    stroke: #ffffff;
    stroke-width: 1px;
    transition: 0.5s fill;
    cursor: pointer;
}

.map--view path:hover {
    fill: #131252 !important;
}
</style>